.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  align-items: center;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-right: 20px;
  position: relative;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  line-height: 60px;
  text-decoration: none;
  transition: all .3s linear;
  display: flex;
}

.nav__link:hover, .nav__link.is-active {
  color: #00acdc;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.catalog {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  place-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .catalog {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .catalog {
    --column-count: 4;
  }
}

* {
  margin: 0;
  padding: 0;
}

body {
  color: #000;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s linear;
}

.card:hover {
  transform: scale(1.2);
}

.card__img {
  width: calc(100% - 6px);
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  text-align: left;
  color: #060b35;
  margin-bottom: 4px;
  font-family: Roboto, serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: all .3s linear;
}

.card:hover .card__title {
  color: #34568b;
}

.card__code {
  text-align: left;
  color: #616070;
  margin-bottom: 16px;
  font-family: Roboto, serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__button {
  box-sizing: border-box;
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  padding-block: 11px;
  font-family: Roboto, serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: all .3s linear;
  display: block;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.reviews {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.reviews__text {
  text-align: right;
  color: #060b35;
  font-family: Roboto, serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.price__text {
  text-align: left;
  color: #616070;
  font-family: Roboto, serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.price__amount {
  text-align: right;
  color: #060b35;
  font-family: Roboto, serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}
